html,
body,
#root,
.wrapper {
  height: 100%;
  background-color: #e5e5e5;
}
.header {
  height: 3rem;
}
.main {
  width: 100%;
}
.wrapper {
  display: flex;
}
.sidebar {
  width: 20rem;
}
.tools .btn {
  color: #707070;
  background-color: #f3f3f3;
  font-size: 1.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tools img {
  width: 1.3rem;
  margin-right: 1rem;
}
.nav {
  padding-right: 0;
}
.nav li {
  padding: 0.5rem 1rem;
  width: 100%;
}
.nav a {
  text-decoration: none;
  color: #292d48;
  display: flex;
  align-items: center;
  width: 100%;
}
.nav a:hover {
  background: #5843be;
  border-radius: 0.5rem;
}
.nav a:hover {
  color: #fff;
}
.nav img {
  height: 2rem;
  width: 2rem;
  margin-right: 1rem;
}
.nav img.formula {
  background: linear-gradient(90deg, #5843be 0%, #8c76f7 100%);
  border-radius: 50%;
}
.header .container-fluid,
.header .row,
.header .col,
.header .right,
.header .right button,
.header .right button img {
  height: 100%;
}
.header .right button img:nth-of-type(3) {
  height: 0.6rem;
}
.header .right button img:nth-of-type(1) {
  height: 1.2rem;
}
.btngroup {
  text-align: right;
  flex-grow: 1;
}
.right {
  display: flex;
}
.has-search {
  display: flex;
  align-items: center;
}
.has-search .form-control {
  padding: 0.5rem 0.75rem;
  padding-left: 4.375rem;
  border-bottom: 0;
  background: #f6f7f8;
  border: 1px solid #eee;
  font-size: 1.2rem;
  border-radius: 0.7rem;
  width: auto;
}
.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 6.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}
input.form-control::-webkit-input-placeholder {
  color: #5843be;
  font-weight: 600;
}
.header button {
  background-color: #f6f7f8;
  color: #5843be;
  padding: 0.5rem 1.5rem;
  font-size: 1.2rem;
}
.header button:hover {
  color: #5843be;
}
.bitcoin {
  display: flex;
  align-items: center;
}
.bitcoin img {
  height: 2.5rem;
}
.bitcoin span {
  color: #5843be;
  font-size: 1.2rem;
  font-weight: 600;
}
.comodity {
  display: flex;
}
.comodity .dropdown:nth-of-type(1) button {
  background: #c30f70;
  color: #fff;
  border: 2px solid #c30f70;
}
.comodity .dropdown:nth-of-type(1) button:hover {
  background: #fff;
  color: #c30f70;
}
.comodity .dropdown:nth-of-type(2) button {
  background: #5843be;
  color: #fff;
  border: 2px solid #5843be;
}
.comodity .dropdown:nth-of-type(2) button:hover {
  background: #fff;
  color: #5843be;
}
.dropdown-toggle::after {
  margin-left: 1rem;
}
.dropdown:nth-of-type(1) .dropdown-menu {
  width: 100%;
  background: #c30f70;
  top: 1rem !important;
}
.dropdown:nth-of-type(1) .dropdown-item:hover {
  background: #fff;
  color: #c30f70;
}
.dropdown-item {
  color: #fff;
  text-align: center;
}
.dropdown:nth-of-type(2) .dropdown-menu {
  width: 100%;
  background: #5843be;
  top: 1rem !important;
}
.dropdown:nth-of-type(2) .dropdown-item:hover {
  background: #fff;
  color: #5843be;
}
.graph {
  position: relative;
}
.graph canvas {
  width: 100% !important;
  height: calc(100vh - 10rem) !important;
  min-height: 480px;
}
.btn:focus {
  outline: 0;
  box-shadow: none;
}
